/* eslint-disable max-len */
import React from 'react';
import SectionTitle from 'components/common/SectionTitle';
import Typography from '@material-ui/core/Typography';
import missionWithBackground from 'components/layout/panels/mission/mission-with-background.png';
import { withTheme } from '@material-ui/core/styles';
import './MissionPanel.scss';

class MissionPanel extends React.PureComponent {
  constructor(props) {
    super(props);

    this.missionBodyText = 'Entrepreneurship Society at UWaterloo was founded in 2011 by 4 mechanical engineering students passionate about entrepreneurship. Now, we’re happy to say we have a diverse range of academic backgrounds with members from EVERY faculty on campus: Arts, Science, Math, Applied Health Studies, Environment, and Engineering.\n' +
      'We like to think of ourselves as the hub for entrepreneurial resources here in the Kitchener-Waterloo area. That means we’re your go-to network when it comes to educating people on developing the entrepreneurial mindset and helping to foster a community of entrepreneurship within newcomers.\n' +
      'We also run events throughout the term that sheds light onto the creative work done in the community because we want to expose students in all faculties to UWaterloo’s startup ecosystem.';
  }

  render() {
    return (
      <div className="mission-panel">
        {/* Image */}
        <div style={{ gridArea: 'image' }}>
          <img
            src={missionWithBackground}
            alt="EntSoc Team at Social"
            width="100%"
            height="auto"
          />
        </div>

        {/* Mission */}
        <div className="mission-text" style={{ gridArea: 'mission' }}>
          <SectionTitle>About Us</SectionTitle>

          <div>
            <Typography variant="title" color="textPrimary" gutterBottom>
              To create a new awareness around entrepreneurship that highlights all opportunities and all aspects of innovation for students at the University of Waterloo.
            </Typography>
          </div>
        </div>
      </div>
    );
  }
}

export default withTheme()(MissionPanel);



