/* eslint-disable no-nested-ternary,react/prop-types,no-return-assign,no-useless-escape */
import Typography from '@material-ui/core/Typography';
import LoadingIcon from 'components/common/LoadingIcon';
import TextButton from 'components/common/TextButton';
import React from 'react';
import Check from '@material-ui/icons/Check';
import MailchimpSubscribe from 'react-mailchimp-subscribe';
import './EmailSignup.scss';

const url = 'https://uwesociety.us4.list-manage.com/subscribe/post?u=eac0a4b72502e9b6b6ccf90f1&amp;id=1082dd8a1b';
const Logger = console;

const validateEmail = (email) => {
  const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
};

export default () => (
  <MailchimpSubscribe
    url={url}
    render={props => <EmailSignup {...props} />}
  />
);

class EmailSignup extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
    };
  }

  _handleInput = (e) => {
    this.setState({
      email: e.target.value,
    });
  };

  render() {
    const { subscribe, status, message } = this.props;
    const { email } = this.state;
    const onSubmit = formData => subscribe && subscribe(formData);
    const submit = () => {
      if (!email) return;
      if (!validateEmail(email)) return;
      onSubmit({
        EMAIL: email,
      });
    };
    const buttonContents = status === 'sending'
      ? <LoadingIcon size="large" />
      : status === 'success'
        ? <Check fontSize="large" style={{ color: 'white' }} />
        : 'SUBMIT';
    if (status === 'error') {
      Logger.error(message);
    }
    return (
      <div className="email-signup">
        <form onSubmit={submit}>
          {status === 'error' && <Typography style={{ color: 'red' }}>An error occurred</Typography>}
          <input
            type="email"
            placeholder="Enter your email address"
            className="email-field"
            value={email}
            onChange={this._handleInput}
            disabled={status === 'sending'}
          />
          <TextButton
            aria-label="Submit email"
            style={{ paddingLeft: 25, paddingRight: 25 }}
            onClick={submit}
            disabled={status === 'success' || status === 'sending' || !validateEmail(email)}
          >
            {buttonContents}
          </TextButton>
        </form>
      </div>
    );
  }
}



