import Banner from 'components/layout/panels/BannerPanel';
import FooterPanel from 'components/layout/panels/FooterPanel';
import GalleryPanel from 'components/layout/panels/GalleryPanel';
import MissionPanel from 'components/layout/panels/MissionPanel';
import PartnerPanel from 'components/layout/panels/PartnerPanel';
import TopBar from 'components/layout/TopBar';
import ScrollableAnchor, { configureAnchors } from 'react-scrollable-anchor';
import React from 'react';
import './LandingPage.scss';


configureAnchors({
  offset: -80,
});

export default class LandingPage extends React.Component {
  render() {
    return (
      <div className="landing-page">
        <TopBar />
        <Banner />
        <ScrollableAnchor id="about">
          <MissionPanel />
        </ScrollableAnchor>
        <ScrollableAnchor id="gallery">
          <GalleryPanel />
        </ScrollableAnchor>
        <ScrollableAnchor id="partners">
          <PartnerPanel />
        </ScrollableAnchor>
        <ScrollableAnchor id="contact">
          <FooterPanel />
        </ScrollableAnchor>
      </div>
    );
  }
}



