import React from 'react';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import './Gallery.scss';

export default class Gallery extends React.PureComponent {
  static propTypes = {
    category: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    albumUrl: PropTypes.string.isRequired,
    images: PropTypes.array.isRequired,
  };

  render() {
    const {
      albumUrl, category, description, title, images,
    } = this.props;
    return (
      <div className="gallery">
        <Typography variant="caption" className="category">{category}</Typography>
        <Typography variant="h6">{title}</Typography>
        <Typography variant="body2" className="event-description">{description}</Typography>
        <div className="image-row-container">
          <a className="facebook-link" href={albumUrl} target="_blank" rel="noopener noreferrer">
            <Typography variant="overline" style={{ display: 'inline-block' }}>
              SEE MORE ON FACEBOOK
            </Typography>
            <span className="chevron" />
          </a>
        </div>
        <div className="image-row">
          { images }
        </div>
      </div>
    );
  }
}



