import Button from '@material-ui/core/Button';
import EmailSignup from 'components/common/EmailSignup';
import React from 'react';
import Typography from '@material-ui/core/Typography';
import Theme from 'Theme';
import FacebookIcon from 'components/common/icon/facebook-icon.svg';
import InstagramIcon from 'components/common/icon/instagram-icon.svg';
import TwitterIcon from 'components/common/icon/twitter-icon.svg';
import EmailIcon from 'components/common/icon/email-icon.svg';
import SVG from 'react-inlinesvg';
import './FooterPanel.scss';

export default class FooterPanel extends React.Component {
  render() {
    return (
      <div className="footer-panel" style={{ backgroundColor: Theme.palette.primary.main }}>
        <Typography variant="h6" className="footer-cta-title" align="center">
          Be the first to know about our awesome events and opportunities this term!
        </Typography>
        <EmailSignup />
        <a href="mailto:info@entsoc.ca" className="email-link"><Typography variant="h4">info@entsoc.ca</Typography></a>
        <Typography variant="body2">200 University Ave W, Waterloo, ON N2L 3G1</Typography>
        <div className="footer-social-row">
          <a href="https://www.facebook.com/UWEntSoc/" target="_blank" rel="noopener noreferrer">
            <Button variant="fab" aria-label="Add">
              <SVG src={FacebookIcon} />
            </Button>
          </a>
          <a href="https://www.instagram.com/uwentsoc/" target="_blank" rel="noopener noreferrer">
            <Button variant="fab" aria-label="Add">
              <SVG src={InstagramIcon} />
            </Button>
          </a>
          <a href="https://twitter.com/UWEntSoc" target="_blank" rel="noopener noreferrer">
            <Button variant="fab" aria-label="Add">
              <SVG src={TwitterIcon} />
            </Button>
          </a>
          <a href="mailto:info@entsoc.ca" target="_blank" rel="noopener noreferrer">
            <Button variant="fab" aria-label="Add">
              <SVG src={EmailIcon} />
            </Button>
          </a>
        </div>
        <Typography>Copyright &copy; 2018 Entrepreneurship Society at UWaterloo</Typography>
      </div>
    );
  }
}



