import React from 'react';
import PropTypes from 'prop-types';
import { withTheme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography/Typography';

class PartnerTemplate extends React.PureComponent {
  static propTypes = {
    image: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    url: PropTypes.string.isRequired,
  };

  render() {
    const {
      image, name, description, url,
    } = this.props;

    return (
      <div className="partner-template-container">
        <div className="partner-image-container">
          <a href={url} target="_blank" rel="noopener noreferrer">
            <img className="partner-image" src={image} alt="accelerator" align="center" />
          </a>
        </div>
        <Typography variant="h5" color="default" align="center" className="partner-title">{ name }</Typography>
        <Typography variant="subtitle1" color="default" align="center">{ description }</Typography>
      </div>
    );
  }
}

export default withTheme()(PartnerTemplate);



