/* eslint-disable react/no-unescaped-entities */
import React from 'react';
import Typography from '@material-ui/core/Typography/Typography';
import ImageRing from 'components/layout/panels/Banner/ImageRing';
import * as ReactDOM from 'react-dom';
import bannerImage1 from './Banner/image_1.jpg';
import bannerImage2 from './Banner/image_2.jpg';
import bannerImage3 from './Banner/image_3.jpg';
import bannerImage4 from './Banner/image_4.jpg';
import bannerImage5 from './Banner/image_5.jpg';
import bannerImage6 from './Banner/image_6.jpg';
import './BannerPanel.scss';

export default class LandingPage extends React.Component {
  constructor(props) {
    super(props);
    this.imageRingArea = React.createRef();
    this.state = {
      size: 0,
    };
  }

  componentDidMount() {
    const area = this.imageRingArea.current;
    this.measureDiv(area);
    window.addEventListener('resize', () => this.measureDiv(area));
  }

  measureDiv = (element) => {
    if (element) {
      // eslint-disable-next-line react/no-find-dom-node
      const rect = ReactDOM.findDOMNode(element).getBoundingClientRect();
      this.setState({
        size: rect.width,
      });
    }
  };

  render() {
    const { size } = this.state;
    const images = [
      bannerImage1, bannerImage2, bannerImage3, bannerImage4, bannerImage5, bannerImage6,
    ];
    return (
      <div className="banner-section">
        <div className="banner-container">
          <div className="banner-text">
            <Typography variant="display2" color="primary" className="main-title">Entrepreneurship Society</Typography>
            <Typography variant="title">EntSoc aims to build inclusive innovation by exposing students of all faculties to Waterloo's entrepreneurial ecosystem</Typography>
          </div>
          <ImageRing
            images={images}
            imageSize={size / 3.5}
            ref={this.imageRingArea}
            onResize={() => this.measureDiv(this.imageRingArea.current)}
          />
        </div>
      </div>
    );
  }
}



