/* eslint-disable react/no-array-index-key */
import React from 'react';
import PropTypes from 'prop-types';

export default class ImageRing extends React.PureComponent {
  static propTypes = {
    images: PropTypes.array.isRequired,
    imageSize: PropTypes.number.isRequired,
    onResize: PropTypes.func.isRequired,
  };

  getImages = () => {
    const { images, imageSize } = this.props;
    const radius = imageSize * 0.8;
    const degreesPerImage = 360 / images.length;
    return images.map((image, i) => {
      const rotation = (degreesPerImage * i + 90);
      const style = {
        width: imageSize,
        height: imageSize,
        margin: -imageSize / 2,
        transform: `rotate(${rotation}deg) translate(${radius}px) rotate(-${rotation}deg)`,
        zIndex: (i % 2) + 1,
      };
      return (
        <div className="banner-image" style={style} key={`ring-image-${i}`}>
          <img src={image} alt="Banner ring" style={{ height: imageSize }} />
        </div>
      );
    });
  };

  render() {
    const { imageSize, onResize } = this.props;
    return (
      <div className="banner-image-wheel" style={{ height: imageSize * 2.6 }} onResize={onResize}>
        { this.getImages() }
      </div>
    );
  }
}



