import React from 'react';
import PartnerTemplate from 'components/layout/panels/PartnerTemplate';
import SectionTitle from 'components/common/SectionTitle';
import './PartnerPanel.scss';

export default class PartnerPanel extends React.Component {
  render() {
    const images = {
      conrad: require('./partnerImages/conrad.png'),
      greenhouse: require('./partnerImages/greenhouse.png'),
      velocity: require('./partnerImages/velocity.jpg'),
      accelerator: require('./partnerImages/accelerator.png'),
    };
    const partnerName = {
      conrad: 'Conrad School of Business and Entrepreneurship',
      greenhouse: 'St. Paul\'s Greenhouse',
      velocity: 'Velocity',
      accelerator: 'Accelerator Centre',
    };
    const partnerDesc = {
      conrad: 'The Conrad School is an immersive educational environment for entrepreneurs. Located within Canada’s most innovative university, the Conrad School is the academic engine for entrepreneurship on campus.',
      greenhouse: 'St. Paul\'s GreenHouse is the first and only live-in campus-linked accelerator in Canada focused on social innovation and entrepreneurship.',
      velocity: 'Velocity is a leading entrepreneurship program at the University of Waterloo, and the most productive startup incubator in Canada. It is committed to providing the knowledge, tools, space and network that startups and entrepreneurs need for success.',
      accelerator: 'The Accelerator Centre (AC) is an award-winning startup accelerator dedicated to building and scaling sustainable, globally competitive companies and giving startups the highest probability of long-term success.',
    };
    return (
      <div className="partner-panel">
        <SectionTitle>Partners</SectionTitle>
        <div className="partner-info-container">
          <PartnerTemplate
            image={images.conrad}
            name={partnerName.conrad}
            description={partnerDesc.conrad}
            url="https://uwaterloo.ca/conrad-school-entrepreneurship-business/"
          />
          <PartnerTemplate
            image={images.greenhouse}
            name={partnerName.greenhouse}
            description={partnerDesc.greenhouse}
            url="https://uwaterloo.ca/stpauls/greenhouse"
          />
          <PartnerTemplate
            image={images.velocity}
            name={partnerName.velocity}
            description={partnerDesc.velocity}
            url="http://velocity.uwaterloo.ca/"
          />
          <PartnerTemplate
            image={images.accelerator}
            name={partnerName.accelerator}
            description={partnerDesc.accelerator}
            url="http://acceleratorcentre.com/"
          />
        </div>
      </div>
    );
  }
}



