import { createMuiTheme } from '@material-ui/core/styles/index';

export default createMuiTheme({
  palette: {
    primary: {
      main: '#B31E36',
      dark: '#4f0e18',
    },
  },
  overrides: {
    MuiButtonBase: {
      disableRipple: true,
    },
    MuiButton: {
      contained: {
        backgroundColor: '#EB5660',
        '&:hover': {
          backgroundColor: '#FB7D85',
        },
      },
      extendedFab: {
        padding: '0 24px',
        '&:hover': {
          backgroundColor: '#FB7D85',
        },
      },
      label: {
        color: '#FFF',
        fontSize: 17,
      },
    },
  },
  typography: {
    fontFamily: [
      '"Montserrat"',
    ],
    display2: {
      fontWeight: 600,
      fontSize: 35,
      lineHeight: 1.2,
    },
    title: {
      fontWeight: 400,
      fontSize: 27,
      lineHeight: 1.5,
    },
    caption: {
      fontSize: 14,
      lineHeight: 1.4,
    },
    h6: {
      fontSize: 24,
      lineHeight: 1.3,
    },
    h5: {
      fontSize: 17,
      lineHeight: 1.4,
      fontWeight: 500,
    },
    h4: {
      fontSize: 21,
      color: '#fff',
    },
    body2: {
      fontWeight: 400,
      fontSize: 17,
      lineHeight: 1.4,
    },
    overline: {
      fontSize: 14,
      lineHeight: 1.4,
    },
    subtitle1: {
      fontSize: 14,
      lineHeight: 1.4,
      fontWeight: 400,
    },
  },
});



